// src/components/ExpenseRecorder.jsx
import React, { useState, useEffect } from "react";
import VoiceExpenseInput from "./VoiceExpenseInput";

const CATEGORIES = ["", "", "", "", "", ""];

export default function ExpenseRecorder({ planId, username, onExpenseAdded, resetTrigger }) {
  const [input, setInput] = useState({ category: "", amount: "", description: "" });
  const [saving, setSaving] = useState(false);
  const [autoDetecting, setAutoDetecting] = useState(false);
  const [expenses, setExpenses] = useState([]);
  const [isExpenseListExpanded, setIsExpenseListExpanded] = useState(true); // 

  // 
  useEffect(() => {
    const fetchExpenses = async () => {
      if (!planId) return;
      
      try {
        const res = await fetch(`http://127.0.0.1:8000/budget/summary/${planId}`);
        const data = await res.json();
        if (data.success) {
          setExpenses(data.items || []);
        }
      } catch (error) {
        console.error(":", error);
      }
    };

    fetchExpenses();
  }, [planId]);

  // resetTrigger
  useEffect(() => {
    if (resetTrigger) {
      setInput({ category: "", amount: "", description: "" });
      setExpenses([]);
    }
  }, [resetTrigger]);

  // ===============  ===============
  const handleAdd = async () => {
    const category = input.category.trim();
    const amount = parseFloat(input.amount);
    if (!category || isNaN(amount) || amount <= 0) {
      alert("");
      return;
    }

    setSaving(true);

    // 
    const requestData = {
      user: username || "guest",
      plan_id: planId,
      category,
      amount,
    };
    
    // 
    if (input.description.trim()) {
      requestData.description = input.description.trim();
    }

    try {
      await fetch("http://127.0.0.1:8000/budget/add", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(requestData),
      });
      
      // 
      setInput({ category: "", amount: "", description: "" });
      
      // 
      const res = await fetch(`http://127.0.0.1:8000/budget/summary/${planId}`);
      const data = await res.json();
      if (data.success) {
        setExpenses(data.items || []);
        // 
        if (onExpenseAdded) {
          onExpenseAdded(data.summary || {});
        }
      }
    } catch (error) {
      console.error(":", error);
      alert("");
    } finally {
      setSaving(false);
    }
  };

  // ===============  ===============
  const handleVoiceExpense = (item) => {
    if (!item) return;
    
    // 
    const newInput = { 
      category: item.category || input.category,
      amount: item.amount || input.amount,
      description: item.text || input.description
    };
    
    setInput(newInput);
    
    // expense/voice-add
    // 
    fetch(`http://127.0.0.1:8000/budget/summary/${planId}`)
      .then(res => res.json())
      .then(data => {
        if (data.success) {
          setExpenses(data.items || []);
          // 
          if (onExpenseAdded) {
            onExpenseAdded(data.summary || {});
          }
        }
      })
      .catch(error => {
        console.error(":", error);
      });
  };

  // ===============  ===============
  const handleAutoCategorize = async () => {
    if (!input.description.trim()) {
      alert("");
      return;
    }

    setAutoDetecting(true);
    try {
      const response = await fetch("http://127.0.0.1:8000/expense/auto-categorize", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ text: input.description }),
      });
      
      const data = await response.json();
      if (data.success) {
        setInput({
          ...input,
          category: data.category,
          amount: data.amount.toString()
        });
      } else {
        alert(": " + data.error);
      }
    } catch (error) {
      console.error(":", error);
      alert("");
    } finally {
      setAutoDetecting(false);
    }
  };

  // ===============  ===============
  const handleDescriptionChange = (e) => {
    const newDescription = e.target.value;
    setInput({ ...input, description: newDescription });
  };

  // 
  const totalExpense = expenses.reduce((sum, expense) => sum + parseFloat(expense.amount), 0);

  return (
    <div style={styles.container}>
      <h3 style={{ marginTop: 0 }}> </h3>
      
      {/*  */}
      <div style={styles.inputSection}>
        <div style={styles.inputRow}>
          <select
            value={input.category}
            onChange={(e) => setInput({ ...input, category: e.target.value })}
            style={styles.select}
          >
            <option value=""></option>
            {CATEGORIES.map((cat) => (
              <option key={cat} value={cat}>{cat}</option>
            ))}
          </select>
          <input
            type="number"
            placeholder=""
            value={input.amount}
            onChange={(e) => setInput({ ...input, amount: e.target.value })}
            style={styles.amountInput}
          />
        </div>
        
        <div style={styles.descriptionRow}>
          <div style={styles.descriptionContainer}>
            <input
              type="text"
              placeholder=""
              value={input.description}
              onChange={handleDescriptionChange}
              style={styles.descriptionInput}
            />
            <button 
              onClick={handleAutoCategorize} 
              disabled={autoDetecting || !input.description.trim()}
              style={styles.autoButton}
            >
              {autoDetecting ? "..." : ""}
            </button>
          </div>
        </div>
        
        <div style={styles.buttonRow}>
          <div style={styles.buttonGroup}>
            <button onClick={handleAdd} disabled={saving} style={styles.addButton}>
              {saving ? "..." : ""}
            </button>
            <div style={styles.voiceInputContainer}>
              <VoiceExpenseInput username={username} planId={planId} onResult={handleVoiceExpense} />
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div style={styles.summary}>
        <p>: <strong>{totalExpense.toFixed(2)}</strong></p>
      </div>

      {/*  */}
      {expenses.length > 0 && (
        <div 
          style={styles.expenseListHeader}
          onClick={() => setIsExpenseListExpanded(!isExpenseListExpanded)}
        >
          <h4 style={styles.expenseListTitle}> </h4>
          <span style={styles.expandIndicator}>
            {isExpenseListExpanded ? ' ' : ' '}
          </span>
        </div>
      )}

      {/*  */}
      {expenses.length > 0 && isExpenseListExpanded && (
        <div style={styles.expenseListContainer}>
          <div style={styles.expenseList}>
            {expenses.map((item, index) => (
              <div key={index} style={styles.expenseItem}>
                <div style={styles.expenseHeader}>
                  <span style={styles.expenseCategory}>{item.category}</span>
                  <span style={styles.expenseAmount}>{item.amount}</span>
                </div>
                {item.description && (
                  <div style={styles.expenseDescription}>{item.description}</div>
                )}
                <div style={styles.expenseDate}>
                  {item.created_at ? new Date(item.created_at).toLocaleString() : ''}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}

